interface DeviceInfo {
  isRealDevice: boolean;
  isMalicious: boolean;
  userAgent: string;
  fingerprint: string;
  riskScore: number;
}

export async function detectDevice(): Promise<DeviceInfo> {
  const userAgent = navigator.userAgent;
  
  // 
  const isRealDevice = checkRealDevice();
  
  // 
  const isMalicious = checkMaliciousActivity();
  
  // 
  const fingerprint = await generateDeviceFingerprint();
  
  // 
  const riskScore = calculateRiskScore(isRealDevice, isMalicious, userAgent);
  
  return {
    isRealDevice,
    isMalicious,
    userAgent,
    fingerprint,
    riskScore
  };
}

function checkRealDevice(): boolean {
  // 
  const suspiciousPatterns = [
    /headless/i,
    /phantom/i,
    /selenium/i,
    /webdriver/i,
    /chrome-headless/i,
    /puppeteer/i,
    /playwright/i,
    /automation/i
  ];
  
  const userAgent = navigator.userAgent.toLowerCase();
  
  //  User Agent
  if (suspiciousPatterns.some(pattern => pattern.test(userAgent))) {
    return false;
  }
  
  //  webdriver 
  if (navigator.webdriver || (window as any).webdriver) {
    return false;
  }
  
  // 
  if ((window as any).phantom || (window as any).__nightmare || (window as any).callPhantom) {
    return false;
  }
  
  //  Chrome DevTools
  if ((window as any).chrome && (window as any).chrome.runtime && (window as any).chrome.runtime.onConnect) {
    return false;
  }
  
  // 
  const { width, height } = screen;
  const commonEmulatorResolutions = [
    [375, 667], [414, 896], [360, 640], [412, 732]
  ];
  
  const isCommonResolution = commonEmulatorResolutions.some(
    ([w, h]) => (width === w && height === h) || (width === h && height === w)
  );
  
  // 
  const hasTouchSupport = 'ontouchstart' in window || navigator.maxTouchPoints > 0;
  
  // 
  const isMobile = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);
  if (isMobile && !hasTouchSupport) {
    return false;
  }
  
  return true;
}

function checkMaliciousActivity(): boolean {
  // 
  const loadTime = performance.now();
  if (loadTime < 100) {
    return true;
  }
  
  // 
  let mouseMovements = 0;
  const checkMouseMovement = () => {
    mouseMovements++;
  };
  
  document.addEventListener('mousemove', checkMouseMovement);
  
  setTimeout(() => {
    document.removeEventListener('mousemove', checkMouseMovement);
    if (mouseMovements === 0) {
      return true; // 
    }
  }, 5000);
  
  // 
  const languages = navigator.languages;
  if (!languages || languages.length === 0) {
    return true;
  }
  
  // 
  const pluginCount = navigator.plugins.length;
  if (pluginCount === 0) {
    return true;
  }
  
  return false;
}

async function generateDeviceFingerprint(): Promise<string> {
  const components = [
    navigator.userAgent,
    navigator.language,
    navigator.platform,
    screen.width,
    screen.height,
    screen.colorDepth,
    new Date().getTimezoneOffset(),
    navigator.hardwareConcurrency,
    navigator.deviceMemory,
    navigator.cookieEnabled
  ];
  
  //  Canvas 
  const canvas = document.createElement('canvas');
  const ctx = canvas.getContext('2d');
  if (ctx) {
    ctx.textBaseline = 'top';
    ctx.font = '14px Arial';
    ctx.fillText('Device fingerprint test', 2, 2);
    components.push(canvas.toDataURL());
  }
  
  //  WebGL 
  const gl = canvas.getContext('webgl');
  if (gl) {
    const debugInfo = gl.getExtension('WEBGL_debug_renderer_info');
    if (debugInfo) {
      components.push(gl.getParameter(debugInfo.UNMASKED_VENDOR_WEBGL));
      components.push(gl.getParameter(debugInfo.UNMASKED_RENDERER_WEBGL));
    }
  }
  
  const fingerprint = components.join('|');
  
  //  SHA-256 
  const msgBuffer = new TextEncoder().encode(fingerprint);
  const hashBuffer = await crypto.subtle.digest('SHA-256', msgBuffer);
  const hashArray = Array.from(new Uint8Array(hashBuffer));
  return hashArray.map(b => b.toString(16).padStart(2, '0')).join('');
}

function calculateRiskScore(isRealDevice: boolean, isMalicious: boolean, userAgent: string): number {
  let score = 0;
  
  if (!isRealDevice) score += 50;
  if (isMalicious) score += 30;
  
  //  UA 
  if (!userAgent || userAgent.length < 50) score += 20;
  
  //  UA
  const botPatterns = [
    /bot/i, /crawler/i, /spider/i, /scraper/i
  ];
  
  if (botPatterns.some(pattern => pattern.test(userAgent))) {
    score += 40;
  }
  
  return Math.min(score, 100);
}